<template lang="pug">
    div.full-width.full-height
        h3 Crossing scenario: #[strong Longitudinal looming]
        h4.center Need of #[strong.red braking]
        loomingCueDefSVG
</template>

<script>
import loomingCueDefSVG from "@/assets/images_src/loomingCueDefinition.svg";
import { TweenMax, TimelineMax, Sine, Power0, _gsScope } from "gsap";

export default {
  name: "LoomingCueDef",
  components: {
    loomingCueDefSVG
  },
  props: {
    step: {
      type: Number,
      default: 1
    }
  },
  watch: {
    step: function(val) {
      this.controlTimeline(val);
    }
  },
  mounted: function() {
    const XrelPos = 120; // longitudinal distance travelled
    const duration = 3;
    const tl = new TimelineMax();
    console.log(this.$refs);
    tl.to(
      "#intPointLine",
      duration,
      {
        attr: {
          d: "M123.145 37.238l" + (-475.46 + 3 * XrelPos) + " 43.4"
        },
        ease: Power0.easeNone
      },
      "start"
    );
    tl.to(
      "#sideview .car",
      duration,
      {
        x: "-=" + XrelPos,
        ease: Power0.easeNone
      },
      "start"
    );
    tl.to(
      "#angleArea",
      duration,
      {
        attr: {
          d: this.getAngleArcD(0.35, 100)
        },
        ease: Sine.easeIn
      },
      "start"
    );
    tl.to("#gamma", duration, { x: "+=55", ease: Sine.easeIn }, "start");
    tl.pause();
    this.$options.timeline = tl;
  },
  methods: {
    getAngleArcD: function(angle, r) {
      //const r = 153.373;
      const cx = r,
        cy = -0.112;
      const endPointX = -29.542 + 153.373 - r,
        endPointY = 37.396;
      var startPointX = -r * Math.cos(angle) + (cx + endPointX);
      var startPointY = r * Math.sin(angle) + (cy + endPointY);
      var d =
        "M" +
        startPointX +
        " " +
        startPointY +
        "a " +
        r +
        " " +
        r +
        " 0 01 " +
        (endPointX - startPointX) +
        " " +
        (endPointY - startPointY) +
        "l" +
        cx +
        " " +
        cy +
        "z";
      return d;
    },
    controlTimeline: function(step) {
      if (step == 2) {
        this.$options.timeline.play();
      } else {
        this.$options.timeline.reverse();
      }
    }
  }
};
</script>

<style scoped>
.red {
  color: rgb(255, 77, 77);
}
</style>
